<script lang="ts">
  import * as XLSX from "xlsx-js-style";
  import { onMount } from "svelte";
  import { writable } from "svelte/store";
  type RowData = [
    string,
    string,
    string,
    string,
    string,
    string,
    string,
    string,
    string,
    string,
    string,
  ];

  let data = writable<RowData[]>([]);
  let basePay = 33000;
  let totalHours = 240;
  let holidays = "";
  let overTimes = "";
  let dayOff = "";
  let weekdayWork = "08:00";
  let weekdayOffWork = "17:00";
  let workStartTime = "17:00";
  let workEndTime = "21:00";
  let engineerName = "";
  let customerName = "";
  let holidaySet = writable<Set<number>>(new Set());
  let overTimeSet = writable<Set<number>>(new Set());
  let selectedYear = new Date().getFullYear();
  let selectedMonth = 6,
    totalWorkDays = 0,
    weekdayOvertimeDays = 0,
    holidayOvertimeDays = 0,
    weekdayOverTime = 0,
    holidayOverTime = 0,
    allOverTime = 0,
    dayOffDays = 0,
    dayOffReduce = 0,
    totalSalary = 0;

  onMount(async () => {
    window.addEventListener("load", () => {
      const savedSettings = localStorage.getItem("settings");
      if (savedSettings) {
        const settings = JSON.parse(savedSettings);
        selectedYear = settings.selectedYear;
        selectedMonth = settings.selectedMonth;
        holidays = settings.holidays;
        overTimes = settings.overTimes;
        weekdayWork = settings.weekdayWork;
        weekdayOffWork = settings.weekdayOffWork;
        workStartTime = settings.workStartTime;
        workEndTime = settings.workEndTime;
        engineerName = settings.engineerName;
        customerName = settings.customerName;
        basePay = settings.basePay;
        totalHours = settings.totalHours;
        dayOff = settings.dayOff;
      }
    });
  });

  function setHolidays() {
    holidaySet = writable<Set<number>>(new Set());
    const holidayArray = holidays.split(",").map(Number);
    holidaySet.set(new Set(holidayArray));
  }

  function setOverTime() {
    overTimeSet = writable<Set<number>>(new Set());
    const overTimeArray = overTimes.split(",").map(Number);
    overTimeSet.set(new Set(overTimeArray));
  }

  function setDayOff() {
    const dayOffArray = dayOff.split(",").map(Number);

    const updatedData = $data.map((row, index) => {
      if (index > 0) {
        const dateStr = row[0];
        const day = parseInt(dateStr.split("/")[1]);
        row[9] = "";
        if (dayOffArray.includes(day)) {
          const clearedRow: RowData = [
            row[0],
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
          ];
          return clearedRow;
        }
      }
      return row;
    });

    data.set(updatedData);
  }

  function generateDates() {
    const daysInMonth = new Date(selectedYear, selectedMonth, 0).getDate();
    const newData: RowData[] = [
      [
        "",
        " ",
        " ",
        " ",
        " ",
        "",
        "",
        "",
        "",
        "",
        "",
      ],
    ];
    for (let i = 1; i <= daysInMonth; i++) {
      const currentDate = new Date(selectedYear, selectedMonth - 1, i);
      const dayOfWeek = [
        "()",
        "()",
        "()",
        "()",
        "()",
        "()",
        "()",
      ][currentDate.getDay()];
      newData.push([
        `${selectedMonth}/${i}${dayOfWeek}`,
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ]);
    }
    data.set(newData);
  }

  function calculateHoursWorkedAndOvertime() {
    const updatedData = $data.map((row, rowIndex) => {
      if (rowIndex > 0) {
        const dateIndex = parseInt(row[0].split("/")[1]);
        const isHoliday = $holidaySet.has(dateIndex);
        const isDayOffDay = row[9].includes("");
        const signInTime = parseTimeToHours(row[1]);
        const signOutTime = parseTimeToHours(row[2]);
        const overtimeSignInTime = parseTimeToHours(row[3]);
        const overtimeSignOutTime = parseTimeToHours(row[4]);

        let workingHours = 0;
        let overtimeHours = 0;

        if (signInTime !== null && signOutTime !== null) {
          workingHours = signOutTime - signInTime;
          workingHours = workingHours > 0 ? Math.max(workingHours - 1, 8) : 0;
        }

        if (overtimeSignInTime !== null && overtimeSignOutTime !== null) {
          overtimeHours = overtimeSignOutTime - overtimeSignInTime;
          overtimeHours =
            overtimeHours > 4 ? Math.min(overtimeHours - 1, 8) : overtimeHours;
        }

        if ((isHoliday || isDayOffDay) && workingHours <= 0) {
          row[7] = "";
          row[8] = "";
        } else {
          row[7] = workingHours.toString();
          row[8] = overtimeHours.toString();
        }
      }
      return row;
    });

    data.set(updatedData);
  }

  function calcOverTimePay(basedPay: number, totalHours: number) {
    const updatedData = $data.map((row, rowIndex) => {
      if (rowIndex > 0) {
        const dateIndex = parseInt(row[0].split("/")[1]);
        const isHoliday = $holidaySet.has(dateIndex);
        const workStartTime = parseTimeToHours(row[3]);
        const workEndTime = parseTimeToHours(row[4]);
        if (workStartTime !== null && workEndTime !== null) {
          const over4Hours = workEndTime - workStartTime > 4;
          const overTimeHours = over4Hours
            ? Math.min(workEndTime - workStartTime - 1, 8)
            : Math.min(workEndTime - workStartTime, 8);
          const basicSalary = calculateBasicSalary(
            basedPay,
            totalHours,
            overTimeHours
          );
          row[10] = basicSalary;
        } else {
          row[10] = isHoliday ? "" : "0";
        }
      }
      return row;
    });

    data.set(updatedData);
  }

  function calculateBasicSalary(
    basedPay: number,
    totalHours: number,
    overTimeHours: number
  ) {
    if (!isNaN(basedPay) && !isNaN(totalHours) && totalHours > 0) {
      const hourlyRate = basedPay / totalHours;
      const firstTwoHours = hourlyRate * 1.34 * Math.min(2, overTimeHours);
      const nextTwoHours = hourlyRate * 1.67 * Math.max(0, overTimeHours - 2);
      const basicSalary = Math.round(firstTwoHours + nextTwoHours);
      return `${basicSalary}`;
    } else {
      return "";
    }
  }

  function parseTimeToHours(time: string): number | null {
    if (!time) return null;
    const [hour, minute] = time.split(":").map(Number);
    if (isNaN(hour) || isNaN(minute)) return null;
    return hour + minute / 60;
  }

  function exportToExcel() {
    const wb = XLSX.utils.book_new();
    const monthTitle = convertToChineseMonth(selectedMonth);
    const ws_data: any = [];
    const titleRow = [
      {
        v: `${monthTitle}`,
        t: "s",
        s: { font: { sz: 24 }, alignment: { horizontal: "center" } },
      },
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
    ];
    ws_data.push(titleRow);

    $data.forEach((row: any) => {
      const holidayDates = Array.from($holidaySet);
      const rowData = row.map((cell: any, columnIndex: number) => {
        const dateIndex = parseInt(row[0].split("/")[1]);
        if (holidayDates.includes(dateIndex)) {
          return {
            v: cell,
            s: {
              fill: { fgColor: { rgb: "c00000" } },
            },
          };
        } else if (row[9].includes("")) {
          return {
            v: cell,
            s: {
              fill: { fgColor: { rgb: "FFFF00" } },
            },
          };
        }
        return cell;
      });
      ws_data.push(rowData);
    });

    ws_data.push(
      [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ":",
        totalWorkDays,
      ],
      [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ":",
        weekdayOvertimeDays,
      ],
      [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ":",
        holidayOvertimeDays,
      ],
      [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ":",
        weekdayOverTime,
      ],
      [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ":",
        holidayOverTime,
      ],
      [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "():",
        allOverTime,
      ],
      [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ":",
        dayOffDays,
      ],
      [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ":",
        dayOffReduce,
      ],
      [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ":",
        totalSalary,
      ]
    );

    const ws = XLSX.utils.aoa_to_sheet(ws_data);

    ws["!merges"] = [{ s: { r: 0, c: 0 }, e: { r: 0, c: 9 } }];

    XLSX.utils.book_append_sheet(wb, ws, monthTitle);

    XLSX.writeFile(
      wb,
      `${convertToChineseMonth(selectedMonth)}_${customerName}_${engineerName}.xlsx`
    );
    alert(
      `${convertToChineseMonth(selectedMonth)}_${customerName}_${engineerName}.xlsx !`
    );
  }

  function savedSetting() {
    localStorage.setItem(
      "settings",
      JSON.stringify({
        selectedYear,
        selectedMonth,
        holidays,
        overTimes,
        weekdayWork,
        weekdayOffWork,
        workStartTime,
        workEndTime,
        engineerName,
        customerName,
        basePay,
        totalHours,
        dayOff,
      })
    );
    alert("!");
  }

  function resetSetting() {
    if (localStorage.getItem("settings") != null) {
      localStorage.removeItem("settings");
      alert("!");
    }
  }

  function updateNames() {
    const engineer = engineerName.trim();
    const customer = customerName.trim();

    const currentData = $data;

    const updatedData = currentData.map((row, rowIndex) => {
      if (rowIndex !== 0) {
        row[5] = "";
        row[6] = "";
        if (row[1] !== "" || row[2] !== "" || row[3] !== "" || row[4] !== "") {
          row[5] = engineer;
          row[6] = customer;
        }
      }
      return row;
    });

    data.set(updatedData);
  }

  function updateWorkTime() {
    holidaySet.subscribe((value: Set<number>) => {
      const holidayDates = Array.from(value);
      const currentData = $data;
      const updatedData = currentData.map((row, rowIndex) => {
        if (rowIndex > 0) {
          const dateIndex = parseInt(row[0].split("/")[1]);
          row[1] = "";
          if (!holidayDates.includes(dateIndex)) {
            row[1] = weekdayWork;
          }
        }
        return row;
      });

      data.set(updatedData);
    });
  }

  function updateWorkOffTime() {
    holidaySet.subscribe((value: Set<number>) => {
      const holidayDates = Array.from(value);
      const currentData = $data;
      const updatedData = currentData.map((row, rowIndex) => {
        if (rowIndex > 0) {
          const dateIndex = parseInt(row[0].split("/")[1]);
          row[2] = "";
          if (!holidayDates.includes(dateIndex)) {
            row[2] = weekdayOffWork;
          }
        }
        return row;
      });

      data.set(updatedData);
    });
  }

  function updateWorkOverTime() {
    overTimeSet.subscribe((value: Set<number>) => {
      const overTimeDates = Array.from(value);
      const holidayDates = Array.from($holidaySet);

      const currentData = $data;
      const updatedData = currentData.map((row, rowIndex) => {
        if (rowIndex > 0) {
          const dateIndex = parseInt(row[0].split("/")[1]);
          if (
            overTimeDates.includes(dateIndex) &&
            !holidayDates.includes(dateIndex)
          ) {
            row[3] = "";
            row[4] = "";
            row[3] = workStartTime;
            row[4] = workEndTime;
          }
        }
        return row;
      });

      data.set(updatedData);
    });
  }

  function calcAll() {
    let totalWorkDaysCalc = 0;
    let weekdayOvertimeDaysCalc = 0;
    let holidayOvertimeDaysCalc = 0;
    let weekdayOvertimeCalc = 0;
    let holidayOvertimeCalc = 0;
    let totalOvertimePayCalc = 0;
    let dayOffDaysCalc = 0;

    $data.slice(1).forEach((row) => {
      const dateStr = row[0];
      const signInTime = row[1];
      const signOutTime = row[2];
      const overtimeSignInTime = row[3];
      const overtimeSignOutTime = row[4];
      const overtimePay = parseFloat(row[10]) || 0;

      const isHoliday = dateStr.includes("()") || dateStr.includes("()");
      const hasSignInOut =
        (signInTime && signOutTime) ||
        (overtimeSignInTime && overtimeSignOutTime);

      if (hasSignInOut) {
        totalWorkDaysCalc++;
      }

      const dayOffNote = row[9].includes("");

      if (isHoliday && overtimeSignInTime && overtimeSignOutTime) {
        holidayOvertimeDaysCalc++;
        holidayOvertimeCalc += overtimePay;
      } else if (!isHoliday && overtimeSignInTime && overtimeSignOutTime) {
        weekdayOvertimeDaysCalc++;
        weekdayOvertimeCalc += overtimePay;
      }

      if (dayOffNote) {
        dayOffDaysCalc++;
      }
    });

    totalOvertimePayCalc = weekdayOvertimeCalc + holidayOvertimeCalc;

    totalWorkDays = totalWorkDaysCalc;
    weekdayOvertimeDays = weekdayOvertimeDaysCalc;
    holidayOvertimeDays = holidayOvertimeDaysCalc;
    weekdayOverTime = weekdayOvertimeCalc;
    holidayOverTime = holidayOvertimeCalc;
    allOverTime = totalOvertimePayCalc;
    dayOffDays = dayOffDaysCalc;
    dayOffReduce = dayOffDays > 0 ? (basePay / 30) * dayOffDays : 0;
    totalSalary = basePay + allOverTime - dayOffReduce;
  }

  function convertToChineseMonth(month: number): string {
    const chineseMonths = [
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
    ];
    return chineseMonths[month - 1] || "";
  }
</script>

<main>
  <h1>{convertToChineseMonth(selectedMonth)}</h1>
  <div class="select-container">
    <div class="left">
      <label for="year"></label>
      <select id="year" bind:value={selectedYear}>
        {#each Array.from({ length: selectedYear }, (_, i) => 1970 + i) as year}
          <option value={year}>{year}</option>
        {/each}
      </select>
    </div>

    <div class="center">
      <label for="month"></label>
      <select id="month" bind:value={selectedMonth}>
        {#each Array.from({ length: 12 }, (_, i) => i + 1) as month}
          <option value={month}>{month}</option>
        {/each}
      </select>
    </div>

    <div class="right">
      <button on:click={generateDates}></button>
    </div>
  </div>

  <div>
    <label for="holidays">1,3,4</label>
    <input id="holidays" type="text" bind:value={holidays} />
    <button on:click={setHolidays}></button>
  </div>
  <div>
    <label for="weekdayWork"></label>
    <input id="weekdayWork" type="time" bind:value={weekdayWork} />
    <button on:click={updateWorkTime}></button>
  </div>
  <div>
    <label for="weekdayOffWork"></label>
    <input id="weekdayOffWork" type="time" bind:value={weekdayOffWork} />
    <button on:click={updateWorkOffTime}></button>
  </div>
  <div>
    <label for="overtimes">1,3,4</label>
    <input id="overtimes" type="text" bind:value={overTimes} />
    <button on:click={setOverTime}></button>
  </div>
  <div>
    <label for="weekdayWork"> </label>
    <input id="workStartTime" type="time" bind:value={workStartTime} />
    <label for="weekdayOffWork"> </label>
    <input id="workEndTime" type="time" bind:value={workEndTime} />
    <button on:click={updateWorkOverTime}></button>
  </div>
  <div>
    <label for="dayOff"></label>
    <input id="dayOff" type="text" bind:value={dayOff} />
    <button on:click={setDayOff}></button>
  </div>
  <div class="centered-container">
    <div class="name-inputs">
      <label for="engineerName"></label>
      <input id="engineerName" type="text" bind:value={engineerName} />
      <label for="customerName"></label>
      <input id="customerName" type="text" bind:value={customerName} />
      <button on:click={updateNames}></button>
    </div>
  </div>
  <div class="centered-container">
    <div class="name-inputs">
      <label for="basedPay"> : </label>
      <input id="basedPay" type="Number" bind:value={basePay} />
      <label for="bonusAmount"> : </label>
      <input id="bonusAmount" type="Number" value="0" />
      <label for="totalHours"> : </label>
      <input id="totalHours" type="Number" bind:value={totalHours} />
    </div>
  </div>
  <button on:click={calculateHoursWorkedAndOvertime}></button>
  <button on:click={() => calcOverTimePay(basePay, totalHours)}
    ></button
  >
  <button on:click={calcAll}></button>
  <button on:click={exportToExcel}>Excel</button>
  <button on:click={savedSetting}></button>
  <button on:click={resetSetting}></button>
  <label for="totalWorkDays">: {totalWorkDays}</label>
  <label for="weekdayOvertimeDays">: {weekdayOvertimeDays}</label>
  <label for="holidayOvertimeDays">: {holidayOvertimeDays}</label>
  <label for="weekdayOverTime">: {weekdayOverTime}</label>
  <label for="holidayOverTime">: {holidayOverTime}</label>
  <label for="allOverTime">(): {allOverTime}</label>
  <label for="dayOffDays">: {dayOffDays}</label>
  <label for="dayOffReduce">: {dayOffReduce}</label>
  <label for="totalSalary">: {totalSalary}</label>
  <table>
    <thead>
      <tr>
        {#if $data && $data.length > 0}
          {#each Object.values($data[0]) as header}
            <th>{header}</th>
          {/each}
        {/if}
      </tr>
    </thead>
    <tbody>
      {#if $data && $data.length > 1}
        {#each $data.slice(1) as row, index}
          <tr class:holiday-row={$holidaySet.has(index + 1)}>
            {#each row as cell, cellIndex}
              <td>
                {#if cellIndex >= 1 && cellIndex <= 4}
                  <input type="time" bind:value={cell} />
                {:else if cellIndex === 9}
                  <input type="text" bind:value={cell} />
                {:else}
                  {cell}
                {/if}
              </td>
            {/each}
          </tr>
        {/each}
      {/if}
    </tbody>
  </table>
</main>

<style>
  main {
    text-align: center;
  }

  h1 {
    margin-bottom: 20px;
  }

  table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }

